import React, {Component} from 'react';
import SeriesList from '../../components/SeriesList';
import Loader from '../../components/Loader';
import Intro from '../../components/Intro';

class Series extends Component{
    state = {
        series: [],
        seriesName: '',
        isFetching: false
    };
    
    //   componentDidMount(){
    //     // const series = ["Game of thrones","India"];
    //     // setTimeout(()=>{
    //     //   this.setState({series});
    //     // },2000);
    //   }
      onSeriesInputChange = e =>{
          this.setState({seriesName: e.target.value,isFetching: true});
        fetch(`http://api.tvmaze.com/search/shows?q=${e.target.value}`)
        .then((response)=>response.json())
        .then(series=>this.setState({series, isFetching: false}))
      }
    render(){
        const {series, seriesName, isFetching} = this.state;
        return (
            <div>
    <Intro message="Hi, this is venkatesh"/>
                <div>
                    <input type="text" value={seriesName} onChange={this.onSeriesInputChange}/>
                </div>
                {
                    !isFetching && series.length===0 && seriesName.trim()===""
                &&
                <p>Please enter series name</p>
                }

                {
                    !isFetching && series.length===0 && seriesName.trim()!==""
                    &&
                    <p>No series found!</p>
                }

                {
                    isFetching && <Loader/>
                }

                {
                    !isFetching && <SeriesList list= {this.state.series}/>
                }
                <SeriesList list = {this.state.series}/>
            </div>
        )
    }
}
export default Series;